import React from "react";
import {
    BrowserRouter as Router,
    Switch,
    Route
} from "react-router-dom";
import Login from "@screens/Dashboard/pages/Login";
import Editor from "@screens/Dashboard/pages/Editor";

const DashboardStack: React.FunctionComponent<any> = (props) => {
    const prepareRouter = (route: string) => {
        return props?.match?.url + route;
    }
    return (
        <Switch >
            <Route path={prepareRouter("/editor")} exact={true}>
                <Editor />
            </Route>
            <Route path={prepareRouter("/login")} exact={true}>
                <Login />
            </Route>
        </Switch>

    )
}


export default DashboardStack;