import axios from 'axios';

export const GET_POSTS = "BLOGS/GET_POSTS";
export const GET_POSTS_SUCCESS = "BLOGS/GET_POSTS_SUCCESS";
export const GET_POSTS_FAILED = "BLOGS/GET_POSTS_FAILED";

export const GET_POSTS_API = "BLOGS/GET_POSTS_API";
export const GET_POSTS_API_SUCCESS = "BLOGS/GET_POSTS_API_SUCCESS";
export const GET_POSTS_API_FAILED = "BLOGS/GET_POSTS_API_FAILED";

const api = axios.create({
    baseURL: 'https://subservices.cangokceaslan.com/api'
})

export const getPosts = (payload = {}) => (dispatch, getStore) => {
    if (getStore().blogs.fetching === true) return;
    dispatch({ type: GET_POSTS })
    return axios.get("https://api.rss2json.com/v1/api.json?rss_url=https%3A%2F%2Fcangokceaslan.medium.com%2Ffeed%3Fcache_delete%3D3")
        .then(function (response) {
            console.log(response.data);
            dispatch({
                type: GET_POSTS_SUCCESS, payload: {
                    author: response?.data?.feed,
                    posts: response?.data?.items
                }
            })
            return response.data;
        })
        .catch(function (error) {
            dispatch({ type: GET_POSTS_FAILED, payload: error })
            console.log(error);
        });
}

export const getPostsFromApi = (payload = {}) => (dispatch, getStore) => {
    if (getStore().blogs.fetchingApi === true) return;
    if (getStore().blogs.apiPostsEnd === true) return;
    const page = getStore().blogs.apiPostsPage;
    dispatch({ type: GET_POSTS_API, payload })
    return api.get(`/posts?page=${page}`)
        .then(function (response) {
            console.log(response.data);
            dispatch({
                type: GET_POSTS_API_SUCCESS, payload: {
                    posts: response.data?.node?.posts
                }
            })
            return response.data;
        })
        .catch(function (error) {
            dispatch({
                type: GET_POSTS_API_FAILED,
                payload: error
            })
            console.log(error);
        });
}

export const getPostFromApi = (payload) => (dispatch, getStore) => {
    //dispatch({ type: GET_POSTS_API, payload })
    return api.get(`/posts/${payload?.id}`)
        .then(function (response) {
            /* console.log(response.data);
            dispatch({
                type: GET_POSTS_API_SUCCESS, payload: {
                    posts: response.data?.node?.posts
                }
            }) */
            return response.data;
        })
        .catch(function (error) {
            /* dispatch({
                type: GET_POSTS_API_FAILED,
                payload: error
            }) */
            console.log(error);
            return error;
        });
}

const initialState = {
    posts: [],
    author: {

    },
    fetching: false,
    apiPosts: [],
    apiPostsPage: 1,
    apiPostsEnd: false,
    fetchingApi: false
}

export default (state = initialState, action) => {
    switch (action.type) {
        case GET_POSTS:
            return {
                ...state,
                fetching: true
            };
        case GET_POSTS_SUCCESS:
            return {
                ...state,
                author: action?.payload?.author,
                posts: (state.posts || []).concat(action.payload?.posts),
            };
        case GET_POSTS_FAILED:
            return state;
        case GET_POSTS_API:
            return {
                ...state,
                fetching: true
            };
        case GET_POSTS_API_SUCCESS:
            return {
                ...state,
                fetchingApi: false,
                apiPosts: (state.apiPosts || []).concat(action.payload?.posts),
                apiPostsPage: state.apiPostsPage + 1,
                apiPostsEnd: action.payload?.posts?.length < 20
            }
        case GET_POSTS_API_FAILED:
            return state;
        default:
            return state;
    }
};
